import { useState, useEffect, useRef } from 'react';
import useMarvelService from './../../services/MarvelSevice';
import ErrorMessage from '../errorMessage/ErrorMessage';
import Spinner from '../spinner/Spinner';
import PropTypes from 'prop-types';
import './charList.scss';

const CharList = (props) => {

    const [charList, setCharList] = useState([]); 
    const [newItemLoading, setNewItemLoading] = useState(false);
    const [offset, setOffset] = useState(0);
    const [charEnded, setCharEnded] = useState(false);
    const [limit, setLimit] = useState(9);

    const {loading, error, getAllCharacters} = useMarvelService();
    
    useEffect(() => {
        getAllCharacters(offset, limit)
        .then(charList => {
            setCharList(charList);
            onCharLoaded();
        })
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);
    
    const onRequest = (offset, limit) => {

        setNewItemLoading(true);
        getAllCharacters(offset, limit)
        .then(onCharListLoaded)
    }
    
    const onCharLoaded = (charEnded = false) => {
        setNewItemLoading(false);
        setOffset(offset => offset + limit);
        setCharEnded(charEnded);    
    }

    const onCharListLoaded = (newCharList) => {
        setCharList(charList => [...charList, ...newCharList]);
        onCharLoaded(newCharList < limit ? true : false);
    }

    const onSelectChar = (id, i) => {
        props.onCharSelected(id);
        focusOnItem(i);
    }

    const itemRefs = useRef([]);

    const focusOnItem = (id) => {
        itemRefs.current.forEach(item => item.classList.remove('char__item_selected'));
        itemRefs.current[id].classList.add('char__item_selected');
        itemRefs.current[id].focus();
    }

    function viewCharItem (charList) {
    
    return charList.map((char, i) => {
            const {id, name, thumbnail} = char;
            const styleNotImg = (thumbnail === "http://i.annihil.us/u/prod/marvel/i/mg/b/40/image_not_available.jpg") ? {objectFit: "contain"} : null;
            
            return (        
                <li className='char__item'
                    tabIndex={0}
                    ref={el => itemRefs.current[i] = el}
                    key={id}
                    onClick={() => onSelectChar(id, i)}
                    onKeyDown={e => {
                        if (e.key === " " || e.key === "Enter") {
                            e.preventDefault();
                            onSelectChar(id, i);
                        }
                    }}
                    >
                    <img src={thumbnail} alt="char" style={styleNotImg}/>
                    <div className="char__name">{ name }</div>
                </li>
            )
        })
    }
    
    const errorMessage = error ? <ErrorMessage /> : null;
    const spinner = loading && !newItemLoading ? <Spinner /> : null;
    const renderItems = viewCharItem(charList);
        
    return (
        <div className="char__list">
        <ul className="char__grid">
            {errorMessage}
            {spinner}
            {renderItems}
        </ul>
        <button 
            className="button button__main button__long"
            disabled={newItemLoading}
            style={{'display': charEnded ? 'none' : 'block'}}
            onClick={() => onRequest(offset, limit)}>
            <div className="inner">load more</div>
        </button>
    </div>
    )

}

CharList.propTypes = {
    onCharSelected: PropTypes.func.isRequired
}

export default CharList;