import { useEffect, useState } from 'react';
import useMarvelService from './../../services/MarvelSevice';

import ErrorMessage from '../errorMessage/ErrorMessage';
import Spinner from './../spinner/Spinner';
import Skeleton from './../skeleton/Skeleton';
import PropTypes from 'prop-types';

import './charInfo.scss';

const CharInfo = ({ charId }) => {
    const [char, setChar] = useState(null);

    const { loading, error, getCharacter, clearError } = useMarvelService();

    useEffect(() => {
        updateChar();
    }, [ charId ]);
    
    const updateChar = () => {
        if (!charId) {
            return;
        }

        clearError();
        getCharacter(charId)
            .then(onCharLoaded)
        }

    const onCharLoaded = (char) => { 
        setChar(char);
    }
    
    const skeleton = (char || loading || error) ? null : <Skeleton />
    const errorMessage = error ? <ErrorMessage /> : null;
    const spinner = loading ? <Spinner /> : null;
    const content = !(loading || error || !char) ? <ViewInfo char={char} /> : null;


    return (
        <div className="char__info">
            {skeleton}
            {errorMessage}
            {spinner}
            {content}
        </div>
    )

}

const ViewInfo = ({char}) => {
    const {name, description, thumbnail, homepage, wiki, comics} = char;
    const styleNotImg = (thumbnail === "http://i.annihil.us/u/prod/marvel/i/mg/b/40/image_not_available.jpg") ? {objectFit: "contain"} : null;

    return (
        <>
            <div className="char__basics">
                <img src={thumbnail} alt={name} style={styleNotImg}/>
                <div>
                    <div className="char__info-name">name</div>
                    <div className="char__btns">
                        <a href={homepage} className="button button__main">
                            <div className="inner">homepage</div>
                        </a>
                        <a href={wiki} className="button button__secondary">
                            <div className="inner">Wiki</div>
                        </a>
                    </div>
                </div>
            </div>
            <div className="char__descr">
                {description}
            </div>
            <div className="char__comics">Comics:</div>
            <ul className="char__comics-list">
                {
                    comics.map((item, i) => {
                        if (i >= 10) {
                            return;
                        }
                        return (
                            <li className="char__comics-item" key={i}>
                                {item.name}
                            </li>
                        )
                    })
                }
                {comics.length ? null : "Ther is no comics"}
            </ul>
        </>
    )
}

CharInfo.propTypes = {
    charId: PropTypes.number
}

export default CharInfo;